<template>
  <v-card>
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> mdi-arrow-left </v-icon>
      </v-btn>
      Consumers
    </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <consumers-grid v-if="params.uuid" :params="params" :getDataCb="getDataCb"> </consumers-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import ConsumersGrid from './consumersGrid.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import IconButton from '../../Common/IconButton.vue';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';
import _get from 'lodash/get';
import ROUTES from '../../../constants/routes';

export default {
  name: 'consumers',
  components: {
    IconButton,
    ConsumersGrid,
    TooltipIconButton,
    Breadcrumbs,
  },
  data() {
    return {
      params: {
        // fetch api flags
        uuid: null,
        with_consumers: true,
      },
      gridData: {},
      breadcrumbItems: [],
    };
  },
  mounted() {
    this.params = { ...this.params, uuid: this.$route.params.streamId };
  },
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
      this.updateBreadCrumbs();
    },
    updateBreadCrumbs() {
      this.breadcrumbItems.push({
        text: 'Flow Network Clone',
        align: 'center',
        disabled: true,
        link: ROUTES.rfFlowNetworkClone,
      });
      this.breadcrumbItems.push({ text: this.$route.params.flowNetworkId, align: 'center', disabled: false });
      this.breadcrumbItems.push({
        text: 'Streams Clone',
        align: 'center',
        disabled: true,
        link: ROUTES.rfFlowNetworkStreamsClone.replace(':flowNetworkId', this.$route.params.flowNetworkId),
      });
      this.breadcrumbItems.push({ text: this.$route.params.streamId, align: 'center', disabled: false });
      this.breadcrumbItems.push({ text: 'Consumers', align: 'center', disabled: true });
    },
  },
};
</script>
