<template>
  <div>
    <generic-grid
      :gridName="gridName"
      :isHeaderConfigurable="true"
      :params="params"
      :service="service"
      :headers="gridHeaders"
      :getDataCb="getDataCb"
      :toolbarTitle="toolbarTitle"
      :storeMutationKey="storeMutationKey"
    >
      <template v-slot:routeLinkName="{ item }">
        <text-button :text="item.name" :onClick="() => redirectTo(dialogNames.viewWriters, item)"></text-button>
      </template>
      <template v-slot:createForm="{ onSave, onClose, isOpen, isSaving }">
        <consumers-form
          v-if="isOpen"
          :stream="stream"
          :isEdit="false"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></consumers-form>
      </template>
      <template v-slot:editForm="{ onSave, item, onClose, isOpen, isSaving }">
        <consumers-form
          v-if="isOpen"
          :item="item"
          :isEdit="true"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></consumers-form>
      </template>
    </generic-grid>
  </div>
</template>
<script>
import ConsumersForm from './consumersForm.vue';
import IconButton from '../../Common/IconButton.vue';
import TextButton from '../../Common/TextButton.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import { rfConsumersService } from '../../../services/rfConsumersService';
import ROUTES from '../../../constants/routes';
import * as gridNames from '../../../constants/gridNames';

const dialogNames = {
  viewWriters: 'viewWriters',
};

const redirectMap = {
  [dialogNames.viewWriters]: (flowNetworkId, streamId, consumerId) =>
    ROUTES.rfWriters
      .replace(':flowNetworkId', flowNetworkId)
      .replace(':streamId', streamId)
      .replace(':consumerId', consumerId),
};

export default {
  name: 'consumers-grid',
  components: { GenericGrid, ConsumersForm, IconButton, TextButton },
  props: ['params', 'getDataCb'],
  data() {
    return {
      dialogNames: dialogNames,
      gridName: gridNames.RF_CONSUMERS,
      service: rfConsumersService,
      storeMutationKey: 'changeRfConsumers',
      toolbarTitle: 'Consumers',
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  computed: {
    streamId() {
      return this.$route.params.streamId;
    },
    flowNetworkId() {
      return this.$route.params.flowNetworkId;
    },
    stream() {
      return {
        uuid: this.params.uuid,
      };
    },
  },
  methods: {
    redirectTo(viewName, item) {
      const consumerId = item.uuid;
      if (!consumerId) {
        return;
      }

      let destination = redirectMap[viewName];
      if (typeof destination === 'function') {
        this.$router.push({ path: destination(this.flowNetworkId, this.streamId, consumerId) });
      }
    },
  },
};
</script>
